  methods: {
    initMap(){
      let { chessBoardGirdSize } = this.getConfig;
      this.pieceData = maps.double.map( item => {
        let pos = this.computPos( item.x, item.y );
        let key = `${ item.y },${ item.x }`;
        let data = {
          key,
          className: `${ item.base } ${ item.type }`,
          type: item.type,
          base: item.base,
          left: pos.left,
          top: pos.top,
          x: item.x,
          y: item.y,
          width: chessBoardGirdSize[ 0 ],
          height: chessBoardGirdSize[ 1 ],
          show: true
        }

        if( !this.chessMap.has( key ) )
          this.chessMap.set( key, data );
        
        return data;
      } )
    },

    clickPiece( row ){
      if( this.redGo ){
        if( row.base === 'black' ) return;
        this.indicatorPos = moves[ row.type ].rule( row, this.chessMap, );
      }else{
        if( row.base === 'red' ) return;
        this.indicatorPos = moves[ row.type ].rule( row, this.chessMap, );
      }    
    },

    clickIndicator( row ){
      let eat, key = `${ row.y },${ row.x }`;
      if( eat = this.chessMap.get( key ) ){
        
        this.chessMap.delete( key ),

        eat.show = false;
      }
      console.log( 'eat', eat );
      let pos = this.computPos( row.x, row.y );
      this.historyStep.push( clone( row.piece ) );
      row.piece.x = row.x;
      row.piece.y = row.y;
      row.piece.left = pos.left;
      row.piece.top = pos.top;
      this.indicatorPos = [];
      this.redGo = !this.redGo;
      console.log( 'this.chessMap', this.chessMap );
    },

    computPos( x, y ){
      let { chessBoardGirdSize, pieceSize, chessBoardLeft, chessBoardTop } = this.getConfig;
      return { 
        left: `${ chessBoardLeft + x * chessBoardGirdSize[ 0 ] - pieceSize / 2}`,
        top: `${ chessBoardTop + y * chessBoardGirdSize[ 1 ] - pieceSize / 2 }`,
      }
    },

    aiGo(){
    //  let val = this.getAlphaBeta( -99999, 99999, 4, this.blackMap , this.redKing );
     console.log( 'val', val );
    },

