      //     //val = val || val.value;
      //     console.log( 'val', val );
      　　	//撤消这个走法;　 
          this.chessMap.get( oldY + ',' + oldX ).x = oldX;
          this.chessMap.get( oldY + ',' + oldX ).y = oldY;

      　　 if (val >= B) { 
            //将这个走法记录到历史表中; 
            //AI.setHistoryTable(txtMap,AI.treeDepth-depth+1,B,my);
