const isInChessboard = ( x, y ) => x >= 0 && x < 9 && y >= 0 && y < 10;//是否在棋盘内
const isInSudoku = ( x, y ) => x > 2 && x < 6 && ( ( y >= 0 && y < 3 ) || ( y <= 9 && y > 6 ) );//是否在九宫格内
//上下左右四个方向
const allDir = [
  { x: 0, y: -1 },
  { x: 1, y: 0 },
  { x: 0, y: 1 },
  { x: -1, y: 0 }
];
const data = {
  king: {
    rule( piece, map ){
      const result = [];
      let obstaclePiece;
      let redKing, blackKing;
      let amongPiece = [];
      for( let dir of allDir ){
        let x = piece.x + dir.x, y = piece.y + dir.y;

        if( !isInSudoku( x, y ) )
          continue;

        if( !( obstaclePiece = map.get( `${ y },${ x }` ) ) || obstaclePiece.base !== piece.base )
          result.push( { x, y, piece } );
      }

      for( let [ key, piece ] of map ){
        if( piece.type === 'king' && piece.base === 'red' )
          redKing = piece;

        if( piece.type === 'king' && piece.base === 'black' )
          blackKing = piece;   
      }

      if( redKing.x === blackKing.x ){
        for( let i = blackKing.y + 1; i < redKing.y; i++ )
          amongPiece.push( map.has( `${ i },${ redKing.x }` ) );

        if( amongPiece.every( item => item === false ) )      
          result.push( { x: blackKing.x, y: blackKing.y, piece }, { x: redKing.x, y: redKing.y, piece } );
      }

      return result;
    },
  },

  guard: {
    rule( piece, map ){
      const result = [];
      let obstaclePiece;

      for( let dir of this.steps ){
        let x = piece.x + dir.x, y = piece.y + dir.y;

        if( !isInSudoku( x, y ) )
          continue;

        if( !( obstaclePiece = map.get( `${ y },${ x }` ) ) || obstaclePiece.base !== piece.base )
            result.push( { x, y, piece } ); 
      }

      return result;
    },

    steps : [ { x: 1, y: -1 }, { x: 1, y: 1 }, { x: -1, y: 1 }, { x: -1, y: -1 } ]
  },

  eleph: {
    rule( piece, map ){
      const result = [];
      let obstaclePiece, obstacPiece;

      for( let i = 0; i < this.steps.length; i++ ){
        let dir = this.steps[ i ], obstacPosition = this.obstac[ i ];
        let x = piece.x + dir.x, y = piece.y + dir.y;
        let obstacX = piece.x + obstacPosition.x, obstacY = piece.y + obstacPosition.y;

        if( !isInChessboard( x, y ) || this.isInDomain( y ) || ( obstacPiece = map.get( `${ obstacY },${ obstacX }` ) ) )
          continue;

        if( !( obstaclePiece = map.get( `${ y },${ x }` ) ) || obstaclePiece.base !== piece.base )
          result.push( { x, y, piece } );  
      }

      return result;
    },
    
    isInDomain( y ){ return y === 3 || y === 6;},
    obstac: [ { x: 1, y: -1 }, { x: 1, y: 1 }, { x: -1, y: 1 }, { x: -1, y: -1 } ],
    steps: [ { x: 2, y: -2 }, { x: 2, y: 2 }, { x: -2, y: 2 }, { x: -2, y: -2 } ] 
  },

  rook: {
    rule( piece, map ){
      const result = [], allDirsEnable = [ 1, 1, 1, 1 ];
      let obstaclePiece, step = 1;

      while( true ){
        if( allDirsEnable.every( ( number ) => number === 0 ) )
          break;
        
        for( let i = 0; i < allDir.length; i++ ){
          let dir = allDir[ i ];
          let x = piece.x + dir.x * step, y = piece.y + dir.y * step;

          if( !isInChessboard( x, y ) ){
            allDirsEnable[ i ] = 0;
            continue;
          }
          
          if( obstaclePiece = map.get( `${y},${x}` ) ){
            if( obstaclePiece.base !== piece.base && allDirsEnable[ i ] ){
              result.push( { x, y } );
              allDirsEnable[ i ] = 0;
              continue;
            }else{
              allDirsEnable[ i ] = 0;
              continue;
            }

          }else if( allDirsEnable[ i ] ){
            result.push( { x, y, price } );
          }
        }   
        step++;  
      }
      return result;
    }
  },

  horse: {
    rule( piece, map ){
      const result = [];

      for( let i = 0;  i < allDir.length; i++ ){
        let dir = allDir[ i ];
        let obstaclePiece;
        let x = piece.x + dir.x, y = piece.y + dir.y;

        if( !map.has( `${ y },${ x }` ) ){
          for( let item of this.steps[ i ] ){
            let x = piece.x + item.x, y = piece.y + item.y;

            if( !isInChessboard( x, y ) )
              continue;

            if( !( obstaclePiece = map.get( `${ y },${ x }` ) ) || piece.base !== obstaclePiece.base )
              result.push( { x, y, piece } );
          }  
        }
      }
      return result;
    },

    steps: [
      [ { x: -1, y: -2 }, { x: 1, y: -2 } ],
      [ { x: 2, y: -1 }, { x: 2, y: 1 } ],
      [ { x: 1, y: 2 }, { x: -1, y: 2 } ],
      [ { x: -2, y: 1 }, { x: -2, y: -1 } ]
    ]
  },

  pawn: {
    rule( piece, map, king ){
      const result = [];
      const isCrossed = piece.base === 'red' ? piece.y < 5 : piece.y > 4;
      for( let dir of allDir ){
        let x = piece.x + dir.x, y = piece.y + dir.y, obstaclePiece;

        if( ( dir.y === 1 && piece.base === 'red' ) || ( ( dir.y === -1 && piece.base === 'black' ) ) )
          continue;

        if( !isInChessboard( x, y ) || ( !isCrossed && dir.x !== 0 ) )
          continue;

        if( !( obstaclePiece = map.get( `${ y },${ x }` ) ) || obstaclePiece.base !== piece.base )
          result.push({ x, y, piece });
      }  

      return result;
    },
  },

  cannon: {
    rule( piece, map ){
      const result = [], allDirsEnable = [ 1, 1, 1, 1 ], obstacles = [ 0, 0, 0, 0 ];
      let step = 1, obstaclePiece;

      while( true ){
        if( allDirsEnable.every( ( number ) => number === 0 ) )
          break;
        
        for( let i = 0; i < allDir.length; i++ ){
          let dir = allDir[ i ];
          let x = piece.x + dir.x * step , y = piece.y + dir.y * step;
          if( !isInChessboard( x, y ) ){
            allDirsEnable[ i ] = 0;
            continue;
          }

          if( obstaclePiece = map.get( `${y},${x}` ) ){
            obstacles[ i ]++; 
            if( obstacles[ i ] === 2 && obstaclePiece.base !== piece.base )
              result.push( { x, y } );
          }else if( !obstacles[ i ] )
            result.push( { x, y, piece } );
        }   
        step++;  
      }
      
      return result;
    },
  }
}

export default data;