    price( x, y, king, map, piece ){
      let pos = [ 0, 2, 4, 6, 8, 6, 4, 2, 0 ];
      let H = Math.abs( x - king.x ) + Math.abs( y - king.y );  
      let G = pos[ x ];
      let sink = 0;
      if( y === 0 || y === 9 ) sink = -4;
      let F = H + G - sink;
      return { x, y, price: -F, piece }
    },

